@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0 auto;
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  min-width: 100vw;
  min-height: 100vh;
  background: #f2f3f4;
}

.headerContainer {
  margin-top: 4vw;
}

.scrollNews {
  margin: 4vw 0;
}

.scrollNews {
  display: flex;
  overflow-x: scroll;
  -ms-scroll-snap-type: x mandatory;
      scroll-snap-type: x mandatory;
}

.articleContainer {
  min-width: 78vw;
  height: 44vh;
  border-radius: 20px;
  margin-left: 4vw;
  scroll-snap-align: center;
  position: relative;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.articleInfo {
  width: 88%;
  border-radius: 20px;
  padding: 6% 8%;
  position: absolute;
  background-color: white;
  margin: 8% auto;
  bottom: 0;
  right: 0;
  left: 0;
}

#title {
  font-size: 4.8vw;
  color: #222;
}

.subHeading {
  width: 100%;
  display: inline-block;
  margin: 8% 0 -4px 0;
  font-weight: 400;
  font-size: 3.8vw;
}

#ctgType {
  color: #5a9bd6;
  text-transform: uppercase;
  float: left;
}

#datePublished {
  color: #707070;
  float: right;
}

.headerContainer {
  position: relative;
  width: 100vw;
  height: 20vh;
}

.logo {
  position: absolute;
  display: flex;
  width: 72vw;
  height: 80%;
  left: 0;
  right: 0;
  margin: 0 auto;
}

.headerContainer img {
  height: 100%;
  max-width: 100%;
  position: absolute;
  align-self: center;
  left: 0;
  right: 0;
  margin: 0 auto;
  height: 100%;
}

.curDay {
  position: absolute;
  display: flex;
  height: 20%;
  right: 0;
  bottom: 0;
  margin-right: 4vw;
}

.curDay h4 {
  font-size: 4vw;
  align-self: flex-end;
  color: #707070;
}

